<template>
	<view class="box">
		<view class="search-box">
			<uni-search-bar radius="100" placeholder="请输入关键字" @confirm="reset" />
		</view>
		<view class="parent-title">
			<titleLine :title-list="title_list" :curTitle='cur_title' @titleChoose="chooseTitle"></titleLine>
		</view>
		<view class="goods">
			<mescroll-body ref="mescrollRef" @init="mescrollInit" @down="downCallback" @up="upCallback" :up="upOption">
				<view class="goods-box" v-if="cur_title==0">
					<template v-for="(item,k) in data_list">
						<navigator class="goods-li" :url="'/pages/goods/detail?seqId='+item.seqId+'&merchantShopId='+item.merchantShopId"
						 :key="k">
							<image lazy-load class="img" :src="item.mainPicUrl" mode="aspectFill"></image>
							<view class="title-box">
								<view class="tips-box" v-if="data_list[k].spuLabel">
									<template v-for="(val,i) in data_list[k].spuLabel.split(',')">
										<view class="width106 " :class="{'bg-btn':i==0,'border-btn':i==1}" :key="i">
											{{val}}
										</view>
									</template>
								</view>
								<view class="title">
									{{item.spuTitle}}
								</view>
								<view class="price-box">
									<view class="num font-bold">￥{{item.promptPrice | formatNum}}</view>
									<!-- <view class="people-num">1001人付款</view> -->
								</view>
							</view>
						</navigator>
					</template>
				</view>
				
				<view class="good" v-if="cur_title==1">
					<template v-for="(item,k) in data_list">
						<view class="shop-li" :key="k">
							<view class="shop-li-t">
								<view class="l">
									<view class="name">
										{{item.shopName}}
									</view>
									<view class="parent-score">
										<score :activeScore="item.score*1"></score>
									</view>
								</view>
								<navigator :url="'/pages/goods/shop?merchantShopId='+item.seqId" class="go-in border-btn">
									进店逛逛
								</navigator>
							</view>
							<view class="shop-good">
								<navigator class="shop-good-li" v-for="(val,i) in item.spuShops" :url="'/pages/goods/detail?seqId='+val.spuSeqId+'&merchantShopId='+item.seqId" :key="i">
									<image lazy-load class="img" :src="val.mainPicUrl" mode="aspectFill"></image>
									<view class="price">
										￥{{val.promptPrice | formatNum}}
									</view>
								</navigator>
							</view>
						</view>
					</template>
					
				</view>
				
				
			</mescroll-body>
		</view>
		<!-- 商家 -->
		
	</view>
</template>

<script>
	import MescrollMixin from "@/components/mescroll-uni/mescroll-mixins.js";
	import MescrollBody from "@/components/mescroll-uni/mescroll-body.vue"
	import uniSearchBar from '@/components/uni-search-bar/uni-search-bar.vue'
	import titleLine from '@/components/title-line.vue'
	import score from '@/components/score.vue'
	export default {
		mixins: [MescrollMixin], // 使用mixin
		data() {
			return {
				page_index: 1,
				page_size: 10,
				no_data: false,
				data_list: [],
				search_text: "",
				spuTitle: "",
				cur_title: '0',
				title_list: [{
						title: '商品',
						id: '0'
					},
					{
						title: '商家',
						id: '1'
					}
				],
				upOption: {
					textNoMore: "没有更多数据",
				},
			}
		},
		components: {
			uniSearchBar,
			titleLine,
			score,
			MescrollBody
		},
		methods: {
			upCallback(page) {
				let pageNum = page.num; // 页码, 默认从1开始
				let pageSize = page.size; // 页长, 默认每页10条
				let api_url = '/o2oSpu/findHeadPage2';
				let obj = {
					queryString: this.search_text,
					spuOrderby: 'orderBySaleAndPrice',
					promptType: 'NONE',//促销类型
					purchaseType: 'ZC', //购买类型
					onSale: 'SALE',
					pageno: pageNum,
					pagesize: pageSize
				}
				if(this.cur_title==1){
					// 商家
					 api_url = '/o2oMerchantShop/findAllShops';
					 obj = {
						merchantShopName: this.search_text,
						pageno: pageNum,
						pagesize: pageSize
					}
				}
				this.$api.get(api_url, {
					params: obj
				}).then((res) => {
					if (res.code && res.code != 200&&res.code != 401) {
						this.mescroll.endErr();
						return
					}
					let data = res;
					// 接口返回的当前页数据列表 (数组)
					let curPageData = data.list;
					// 接口返回的当前页数据长度 (如列表有26个数据,当前页返回8个,则curPageLen=8)
					let curPageLen = curPageData.length;
					// 接口返回的总页数 (如列表有26个数据,每页10条,共3页; 则totalPage=3)
					let totalPage = data.pages;

					//设置列表数据
					if (page.num == 1) this.data_list = []; //如果是第一页需手动置空列表
					this.data_list = this.data_list.concat(curPageData)
					// 请求成功,隐藏加载状态
					//方法一(推荐): 后台接口有返回列表的总页数 totalPage
					this.mescroll.endByPage(curPageLen, totalPage);
				})
			},
			reset(res) {
				this.search_text = res.value;
				this.page_index = 1;
				this.data_list = [];
				this.mescroll.resetUpScroll(true);
				this.mescroll.scrollTo(0, 0);
			},
			chooseTitle(e) {
				this.cur_title = e.id
				this.page_index = 1;
				this.data_list = [];
				this.search_text="";
				this.mescroll.resetUpScroll(true);
				this.mescroll.scrollTo(0, 0);
			}
		}
	}
</script>

<style lang="scss">
	.parent-title {
		width: calc(100% - 68rpx);
		height: auto;
		margin: 0 auto;
	}

	.example-body {
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: center;
		padding: 0;
		font-size: 14px;
		background-color: #ffffff;
	}

	// 搜索商家
	.good {
		.shop-li {
			background: white;
			width: calc(100% - 60rpx);
			padding: 40rpx 30rpx;
			margin-bottom: 16rpx;

			.shop-li-t {
				font-size: 36rpx;
				display: flex;
				align-items: center;
				justify-content: space-between;

				.l {
					width: 520rpx;
					flex-shrink: 0;

					.name {
						width: 100%;
						overflow: hidden;
						text-overflow: ellipsis;
						white-space: nowrap;
					}

				}

				.go-in {
					width: 142rpx;
					height: 60rpx;
					padding: 0;
					line-height: 60rpx;
					text-align: center;
				}

				.parent-score {
					margin: 20rpx 0;
				}
			}

			.shop-good {
				width: 100%;
				display: flex;

				.shop-good-li {
					width: 220rpx;
					height: 170rpx;
					background: $color-primary;
					margin-left: 20rpx;
					position: relative;

					.img {
						width: 100%;
						height: 100%;
					}

					.price {
						position: absolute;
						bottom: 12rpx;
						right: 20rpx;
						padding: 8rpx;
						background: rgba(0, 0, 0, 0.4);
						border-radius: 30rpx;
						font-size: 22rpx;
						color: white;
					}
				}

				.shop-good-li:nth-child(1) {
					margin: 0
				}
			}
		}
	}


	// 搜索商品
	.goods {
		height: auto;
		margin: 20rpx auto;
	}

	.search-btn {
		border-radius: 18px;
		font-size: 12px;
		font-weight: bold;
		color: #157658;
		text-align: center;
		width: 100rpx;
		height: 60rpx;
		line-height: 60rpx;
		background: $color-primary;
		color: white;
		flex-shrink: 0;
		flex-grow: 0;
	}

	.search-box {
		flex-direction: row;
		flex-wrap: wrap;
		justify-content: center;
		padding: 0;
		font-size: 14px;
		background-color: #ffffff;


		.left {
			width: 400rpx;
			height: 64rpx;
			background: white;
			border-radius: 32rpx;
			align-items: center;
			display: flex;
			padding: 0 70rpx;
			overflow: hidden;
			color: #959595;
			font-size: 24rpx;

			.iconfont {
				font-size: 30rpx;
			}

			.serarch {
				flex-grow: 1;
				height: 64rpx;
				font-size: 24rpx;
				margin-left: 20rpx;
			}
		}

		.iconshouye-xiaoxi {
			font-size: 36rpx;
			color: #959595;
			position: relative;

			.dot {
				width: 14rpx;
				height: 14rpx;
				background: $color-primary;
				border-radius: 100%;
				position: absolute;
				top: -4rpx;
				right: -6rpx;
				z-index: 1;
			}
		}
	}

	.goods-box {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		width: calc(100% - 68rpx);
		margin: 0 auto;

		.goods-li {
			flex-shrink: 0;
			width: 332rpx;
			background: rgba(255, 255, 255, 1);
			border-radius: 8rpx;
			overflow: hidden;
			margin-bottom: 34rpx;

			.img {
				width: 332rpx;
				height: 440rpx;
				background: $color-primary;
			}
		}
	}

	.width106 {
		height: auto;
		font-size: 20rpx;
		margin-right:10rpx;
		padding: 6rpx 14rpx;
	}

	.title-box {
		width: calc(100% - 40rpx);
		padding: 20rpx 20rpx;
		padding-top: 0;

		.tips-box {
			display: flex;
		}

		.title {
			font-size: 30rpx;
			margin: 5rpx 0;
			overflow: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;

		}

		.price-box {
			width: 100%;
			display: flex;
			align-items: center;

			.num {
				font-size: 32rpx;
				color: $color-primary;
			}

			.people-num {
				font-size: 20rpx;
				margin-left: 40rpx;
			}
		}
	}
</style>
